/* Preview and Edit buttons (with strings as values) */
.main-button {
  margin-bottom: 30px;
  padding: 10px 20px;
  background-color: var(--grey);
  font-family: 'Raleway-SemiBold';
  font-size: 1.3rem;
  color: var(--dark-blue);
  border: none;
  border-radius: 2px;
  transition: all 0.3s;
}

.main-button:hover {
  background-color: var(--green);
  cursor: pointer;
  transition: all 0.3s;
}

/* Font Awesome buttons (with icons as values) */
.fontAwesome-button {
  height: 40px;
  margin: 0 5px;
  padding: 10px;
  background-color: var(--grey);
  color: var(--dark-blue);
  font-size: 1rem;
  border: none;
  border-radius: 50%;
  transition: all 0.3s;
}

.fontAwesome-button:hover {
  cursor: pointer;
  transition: all 0.3s;
}

/* Deleting and adding info */
.delete-button,
.add-button {
  margin-top: 5px;
  float: right;
}

/* Adding skill */
.add-skill-button {
  margin-top: 20px;
}

.delete-button:hover {
  background-color: var(--red);
  cursor: pointer;
  transition: all 0.3s;
}

.add-button:hover,
.add-skill-button:hover {
  background-color: var(--green);
  cursor: pointer;
  transition: all 0.3s;
}

/* Font Awesome buttons hovers colors */
.green-hover:hover {
  background-color: var(--green);
}

.red-hover:hover {
  background-color: var(--red);
}

.blue-hover:hover {
  background-color: var(--light-blue);
}

.orange-hover:hover {
  background-color: var(--orange);
}

.purple-hover:hover {
  background-color: var(--purple);
}
